/* Home view styling */

#bio-view {
  background-color: #f5f5f5;
  display: flex;
  padding: 4rem 2rem;
  text-align: left;
  justify-content: center;
  min-height: 100vh;
}

/*bio typography */

.bio-blurb {
  letter-spacing: 0.02em;
  font-size: 1.125rem;
  line-height: 1.75rem;
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
}

/* headline styles */

.greeting {
  white-space: pre-wrap;
  flex-direction: row;
  justify-content: center;
  font-size: 1.5rem;
}

.g-1, .g-2 {
  margin: 0;
  display: inline-block;
}

p.motto {
  font-size: 1rem;
  margin-bottom: 0;
}

p.brain {
  font-size: .75rem;
}

/* bio photo style */

.avatar {
  display: flex;
  align-items: flex-start;
  justify-content: center;
}

#avatar {
  width: 200px;
  height: auto;
  margin-bottom: 1rem;
  background-color: #d45f15;
  border: 8px solid #d45f15;
}

.clear {
  clear: left;
}

/* media queries for mobile-first responsive design */

@media(min-width: 576px) {

  .jumbotron {
    text-align: justify;
  }

  .avatar {
    display: inline;
  }

  #avatar {
    float: left;
    margin-right: 2rem;
    margin-bottom: 0;
    shape-outside: circle();
    border-radius: 50%;
  }

  .greeting {
    margin: 0;
    font-size: 1.75rem;
    justify-content: left;
  }

  .g-1 {
    margin-left: -1.4rem;
    margin-right: 0;
  }

  .g-2 {
    margin-left: 0;
  }

}

@media(min-width: 768px) {

  .greeting {
    font-size: 2rem;
  }

  .bio {
    margin-top: 2.75rem;
  }

  .bio-blurb {
    font-size: 1.3125rem;
    line-height: 1.99rem;
    margin-bottom: 2.5rem;
  }
  
  p.motto {
    font-size: 1.25rem;
  }

}

@media(min-width: 992px) {

  .greeting {
    font-size: 2rem;
  }

  .bio-blurb {
    font-size: 1.5rem;
    line-height: 2.1rem;
    margin-bottom: 2.5rem;
  }

  p.motto {
    font-size: 1rem;
  }

}

@media(min-width: 1200px) {

  .greeting {
    font-size: 3rem;
  }

  .bio-blurb {
    font-size: 2rem;
    line-height: 2.98rem;
    margin-bottom: 2.5rem;
  }

  p.motto {
    font-size: 1.75rem;
  }
  
}